.etc_body {
    padding-top: 100px;
    margin-left: 13%; 
    margin-right: 13%;

    position:relative;  /*문제 생길시 지우기*/
    min-height: 100vh;

    background-color: white;
    box-shadow: 3px 3px 5px #888888;
}


table {
    border-collapse: collapse;
    width: 100%;
    margin: 0;
}

th, td {
    border-style: solid;
    border-color:white;
    background-color: whitesmoke;
    border-width: 1px 2px;
}

td {
    height: 40px;
}

td a{
    display: block;
    text-decoration: none;
    color: black;
    padding:10px 0 10px 10px;
}

td a:hover{
    background-color: #888888;
}
