.body {
    padding-top: 150px;
    margin-left: 13%; 
    margin-right: 13%;

    position:relative;  /*문제 생길시 지우기*/
    min-height: 100vh;

    background-color: rgba(255,255,255,0.6);
    box-shadow: 3px 3px 5px #888888;
}


.gridbody{
    display: grid;
	grid-template-columns: repeat(auto-fill, 300px);
	grid-column-gap: 30px;
	grid-row-gap: 30px;

    justify-content: center;
}

