.posterBody {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white; 
    width: 300px;
    height: 400px;

    margin: 0;

    box-shadow: 3px 3px 5px #888888;
}

.picture {
    width: 250px; 
    height: 250px; 
    border-style: solid; 
    margin: 20px 0;
}

.picture img{
    width: 250px;
    height: 250px;
}

.title {
    margin-top: 0px;
    margin-bottom: 10px;
    font-size: 30px;
}
