.header {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #212121;
    position: fixed;
    top: 0;
    width: 100%;
  
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    border-color: gray;
  }
  
  .header li {
    float: left;
  }
  
  .header li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  .header li a:hover {
    text-decoration: underline;
  }
  
  .active {
    text-decoration: underline !important;
  }

  .profile {
    width: 25px;
    height: 25px ;
    border-radius: 50% ;
    object-fit: cover ;
  }